<template>
  <div>
    <div class="title">
      <!-- 总订单 -->
      <div class="homeInfo">
        <div class="img-div">
          <img src="@/assets/imgs/allorder.png" />
        </div>
        <div>
          <p class="home-p">总订单</p>
          <p class="num-p">{{totalOrder}}</p>
        </div>
      </div>
      <!-- 总销售额 -->

      <div class="homeInfo">
        <div class="img-div">
          <img src="@/assets/imgs/coin.png" />
        </div>
        <div>
          <p class="home-p">总销售额</p>
          <p class="num-p">{{totalAmount}}</p>
        </div>
      </div>
      <!-- 今日订单 -->

      <div class="homeInfo">
        <div class="img-div">
          <img src="@/assets/imgs/order.png" />
        </div>
        <div>
          <p class="home-p">今日订单</p>
         <p class="num-p">{{todayOrder}}</p> 
        </div>
      </div>
      <!-- 今日销售 -->

      <div class="homeInfo">
        <div class="img-div">
          <img src="@/assets/imgs/money.png" />
        </div>
        <div>
          <p class="home-p">今日销售</p>
          <p class="num-p">{{totayAmount}}</p>
        </div>
      </div>
    </div>
    <!-- echarts数据报表 -->
    <div id="charts"></div>
  </div>
</template>

<script>
import echarts from "echarts"; //引入echarts
import { API_GET_HOME_ECHARTS } from "@/API/apis.js"; //引入接口文件
export default {
  data() {
    return {
      // 总订单
      totalOrder: 0,
      // 总销售额
      totalAmount: 0,
      // 今日订单
      todayOrder: 0,
      // 今日销售额
      totayAmount: 0
    };
  },
  // vue生命周期mounted阶段
  mounted() {
    // 初始化DOM容器
    var homeEacharts = echarts.init(document.getElementById("charts"));
    
    // showLoading加载动画
    homeEacharts.showLoading()
    // 发送请求获取报表
    API_GET_HOME_ECHARTS().then(res => {
      console.log(res);
      // 解构服务器返回的数据
      let {
        orderData,
        amountData,
        xData,
        totalOrder,
        totalAmount,
        todayOrder,
        totayAmount
      } = res.data;
      // 转整数，转本地字符串
      this.totalOrder = parseInt(totalOrder).toLocaleString();
      this.totalAmount = parseInt(totalAmount).toLocaleString();
      this.todayOrder = parseInt(todayOrder).toLocaleString();
      this.totayAmount = parseInt(totayAmount).toLocaleString();
      // 配置表格对象
      let option = {
        // 报表标题
        title: {
          text: "数据统计"
        },
        tooltip: {
          trigger: "axis"
        },
        // 数据总览
        legend: {
          data: ["订单数据", "金额数据"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        // x轴坐标数据
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: xData
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "订单数据",
            type: "line",
            stack: "总量",
            data: orderData
          },
          {
            name: "金额数据",
            type: "line",
            stack: "总量",
            data: amountData
          }
        ]
      };
      // 进行表格渲染
      homeEacharts.setOption(option);
      // 隐藏showLoading加载动画
      homeEacharts.hideLoading()
    });
  }
};
</script>

<style lang="less" scoped>
.title {
  display: flex;
  justify-content: space-between;
  > .homeInfo {
    display: flex;
    width: 260px;
    height: 80px;
    background-color: rgba(56, 219, 170, 0.411);
    align-items: center;
    text-align: center;
    justify-content: space-around;
    border-radius: 10px;
    .home-p {
      margin: 0px;
      color: rgb(104, 103, 103);
      font-size: 18px;
      font-weight: bold;
      margin-top: 10px;
    }
    .num-p{
      margin-top: 5px;
      font-weight: bold;
      font-family: "黑体";
      font-size: 17px;
    }
    > .img-div {
      width: 50px;
      height: 50px;
      vertical-align: middle;
      display: table-cell;
      text-align: center;
      img {
        width: 50px;
        height: 50px;
      }
    }
  }
}
#charts {
  margin-top: 40px;

  height: 400px;
  background-color: #fff;
  border-radius: 8px;
  padding: 10px;
}
</style>